/**
 * Responsive Text
 */

html {
  font-family: 'Raleway', sans-serif;
  font-size: 18px;

  @include max-screen(1000px) {
    font-size: 17px;
  }

  @include max-screen(800px) {
    font-size: 16px;
  }

  @include max-screen(600px) {
    font-size: 15px;
  }

  @include max-screen(500px) {
    font-size: 14px;
  }

  @include max-screen(400px) {
    font-size: 13px;
  }
}

h1 {
  font-size: 3rem;
  color: $blue-primary;
}



